.container {
    padding-left: 50px;
    box-sizing: border-box;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.counter_container {
    margin-left: 25vw;
    width: 30vw;
    height: 50vh;
    border: 1px solid rgba(255, 255, 255, 0.56);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}