* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: white;
    user-select: none;
}

.App {
    text-align: center;
}

.App-main {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-main h2 {
    word-wrap: break-word;
    padding-left: 50px;
    padding-right: 50px;
}

.App-main h2::selection {
    color: #282c34;
    background-color: rgb(255, 176, 189);
}

.counter-variants-to-select {
    width: 40vw;
    display: flex;
    justify-content: space-evenly;
}

.counter-view-1 {
    min-width: 200px;
    max-width: 320px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 15vw;
    height: 150px;
    border: 1px solid rgba(255, 255, 255, 0.44);
    transition: all 0.5s ease;
}

.counter-view-1:hover {
    cursor: pointer;
    box-shadow: 0 2px 15px rgba(255, 255, 255, 0.84);
    border: 1px solid #ffffff;
    transform: scale(1.01);
    border-radius: 15px;
}

.counter-view-presentation1 {
    height: 80%;
    width: 80%;
}

.presentation1-counter {
    padding-top: 5px;
    font-size: 1.5rem;
    background-color: rgba(255, 255, 255, 0.16);
    height: 45%;
    user-select: none;
}

.presentation1-settings {
    margin-top: 15px;
    padding: 4px 4px 7px 4px;
    width: 30%;
    margin-left: auto;
    font-size: 0.75rem;
    background-color: rgba(11, 173, 214, 0.62);
    border-radius: 5px;
    user-select: none;
}

.counter-view-2 {
    min-width: 200px;
    max-width: 320px;
    display: flex;
    justify-content: space-evenly;
    padding: 10px;
    width: 15vw;
    height: 150px;
    border: 1px solid rgba(255, 255, 255, 0.44);
    transition: all 0.5s ease;
}

.counter-view-2:hover {
    cursor: pointer;
    box-shadow: 0 2px 15px rgba(255, 255, 255, 0.84);
    border: 1px solid #ffffff;
    transform: scale(1.01);
    border-radius: 15px;
}

.presentation2 {
    margin-top: 10px;
    padding: 10px;
    width: 35%;
    height: 80%;
    border-radius: 5px;
    font-size: 0.8rem;
}

.settings-2 {
    user-select: none;
    background-color: rgba(246, 120, 30, 0.66);
}

.counter-2 {
    user-select: none;
    background-color: rgba(255, 255, 255, 0.16);
}


/* ----------- */


@media (max-width: 1150px) {
    .counter-variants-to-select {
        width: 60vw;
        justify-content: space-evenly;
    }
}

@media (max-width: 1000px) {
    .App-main {
        max-height: 100vh;
        padding-top: 70px;
        flex-direction: column;
    }

    .counter-variants-to-select {
        flex-direction: column;
        align-items: center;
    }

    .counter-view-1, .counter-view-2 {
        margin-top: 30px;
    }
}
















